import { css } from "@emotion/react";
import FadeLoader from "react-spinners/FadeLoader";
interface Props {
  inverted?: boolean;
  content?: string;
}

const override = css`
  display: block;
  margin: 0 auto;
  border-color: "yellow";
`;

export default function Loading({ content = "Loading...", inverted = true }: Props) {
  return <FadeLoader css={override} color="#f1b722" />;
}
