/* eslint-disable jsx-a11y/iframe-has-title */
import { observer } from "mobx-react-lite";
import React, { useEffect } from "react";
import { Col, Container, Row } from "react-bootstrap";
import Switch from "react-bootstrap/esm/Switch";
import { Route, useParams, useRouteMatch } from "react-router";
import { Link } from "react-router-dom";
import { useStore } from "../../App/Store/store";
import "./Detail.css";
import IFrame from "./IFrame";

export default observer(function Detail() {
  const {
    movieStore: { loadMovie, clearSelectedMovie, selectedMovie: movie },
  } = useStore();
  const { id } = useParams<{ id: string }>();
  let { url } = useRouteMatch();

  useEffect(() => {
    if (id) loadMovie(id);
    window.scrollTo(0, 0);
    return () => clearSelectedMovie();
  }, [id, loadMovie, clearSelectedMovie]);
  if (!movie) return null;
  return (
    <Container className="">
      <Row className="header-content-block  ">
        <Col lg={5}>
          <div className="movie__image" style={{ backgroundImage: `url(${movie.image})` }}></div>
        </Col>
        <Col lg={7}>
          <h1 className="title__movie">{movie.title}</h1>
          <div className="info-wrapper">
            <div className="preview__film">
              <div className="time">
                <h6 className="preview__label">Thời lượng:</h6>
                <h6 className="preview__content">{movie.time}</h6>
              </div>
              <div className="director">
                <h6 className="preview__label">Đạo diễn:</h6>
                <h6 className="preview__content">{movie.director}</h6>
              </div>
              <div>
                <i style={{ color: "yellow" }} className="far fa-clock"></i> {movie.publishYear}
              </div>
            </div>
            <div className="review__film">
              <p>{movie.description}</p>
            </div>
          </div>
        </Col>
      </Row>
      <div style={{ width: "100%", textAlign: "center", marginTop: 30 }}>
        {movie.chapUrl.length !== 0
          ? movie.chapUrl.map((chap, i) => (
              <div style={{ display: "inline-flex", textAlign: "center" }} key={i}>
                <Link to={`${url}/chap/${i + 1}`}>
                  <button className="btn btn-light m-2">{i + 1}</button>
                </Link>
              </div>
            ))
          : ""}
      </div>

      <Switch>
        <Route exact path={`${url}/chap/:chap`}>
          <IFrame chapUrl={movie.chapUrl} />
        </Route>
        {/* <Route exact path={`${url}`}>
          <IFrame chapUrl={movie.chapUrl} />
        </Route> */}
      </Switch>
    </Container>
  );
});
