/* eslint-disable jsx-a11y/iframe-has-title */
import { observer } from "mobx-react-lite";
import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import { Row } from "react-bootstrap";
import { useParams, useRouteMatch } from "react-router";
import { ChapUrl } from "../../App/Models/chapUrl";

interface Props {
  chapUrl: ChapUrl[];
}

export default observer(function IFrame({ chapUrl }: Props) {
  let { path, url } = useRouteMatch();
  const { chap } = useParams<{ chap: string }>();
  console.log(path);
  console.log(url);
  const [link, setLink] = useState("");
  useEffect(() => {
    const temp = chapUrl!.find((x) => x.chap === parseInt(chap));
    if (chap === "0" || temp === undefined) {
      setLink(chapUrl[0].url);
    }
    if (temp && temp!.chap !== 0) {
      setLink(temp.url);
    }
    return () => {};
  }, [setLink, chapUrl, chap]);
  return (
    <Row>
      <div
        className="iframe__container"
        style={{ width: "100%", height: 0, position: "relative", paddingBottom: "56.250%", marginTop: 40 }}
      >
        <iframe
          allow="autoplay"
          src={`${link}`}
          style={{ width: "100%", height: "100%", position: "absolute", left: 0, top: 0, overflow: "hidden" }}
          frameBorder={0}
          allowFullScreen={true}
        ></iframe>
      </div>
    </Row>
  );
});
