import { observer } from "mobx-react-lite";
import React from "react";
import { Movie } from "../../App/Models/movie";
import { Link } from "react-router-dom";

interface Props {
  movie: Movie;
}
function truncate(str: string) {
  return str.length > 40 ? str.substring(0, 35) + "..." : str;
}
export default observer(function CardMovie({ movie }: Props) {
  return (
    <div className="movie-item">
      <Link
        style={{ textDecoration: "none" }}
        to={movie.category === "phim bo" ? `/phim/${movie.id}/chap/1` : `/phim/${movie.id}/chap/0`}
      >
        <img className="movie-item__img" alt="img" src={movie.image} />
      </Link>
      <Link style={{ textDecoration: "none" }} to={`/phim/${movie.id}`}>
        <div className="movie-item__title">{truncate(movie.title)}</div>
      </Link>
    </div>
  );
});
