import { Formik } from "formik";
import React from "react";
import { useStore } from "../../App/Store/store";

export default function Filter() {
  const {
    movieStore: { setPredicate, loadMovies },
  } = useStore();
  function Search(value: string) {
    setPredicate("title", value);
    loadMovies().then(() => {
    });
  }

  return (
    <div className="filter__container">
      <div className="filter__bar">
        <Formik
          initialValues={{ search: "" }}
          onSubmit={(values, { setSubmitting }) => {
            Search(values.search);
            // setSubmitting(false);
          }}
        >
          {({ values, isSubmitting, handleBlur, handleChange, handleSubmit }) => (
            <form className="filter__form" onSubmit={handleSubmit}>
              <input
                className="filter__input"
                placeholder="Tìm kiếm"
                type="text"
                name="search"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.search}
              />
              <button className="filter__btn" type="submit" disabled={isSubmitting}>
                Tìm
              </button>
            </form>
          )}
        </Formik>
      </div>
    </div>
  );
}
